<script>
import FooterModal from '../../components/footerModal.vue';
import Meta from '../../components/Meta.vue';

export default {
    components: { Meta, FooterModal },
    
    data () {
        return {
        meta: {
            title:"HiBAR",
            image:"test image",
            description:"HiBAR",
        },
        data:{}
        }
    },

    mounted(){
        this.axios
            .get("https://api.hi8.tw/hi8/v1/share/product/"+this.$route.params.id,
            {
                headers:{
                    'Apikey':"STEALING11111API22222NO33333DICK"
                }
            })
            .then((response)=>{
                this.data = response.data.data

                // 為了將運費細分出最低與最高
                let shipping_system_obj = JSON.parse(response.data.data.shipping_system)
                let shipping_system = []
                
                let arr = Object.keys(shipping_system_obj)
                arr.map((item)=>{
                    shipping_system.push(shipping_system_obj[item])
                })
                let shipping_max_price = Math.max(...shipping_system.map(p => p.price))
                let shipping_min_price = Math.min(...shipping_system.map(p => p.price))
                this.$set(this.data,"shipping_max_price",  shipping_max_price)
                this.$set(this.data,"shipping_min_price",  shipping_min_price)
                // 

                this.meta = {
                    title:"HiBAR - 商品分享",
                    image:this.data.dato,
                    description:this.data.description,
                }
            })
    },
    methods:{
        openModal:function(){
            this.$bvModal.show('my-modal')
        },
    }
}
</script>