<script>
import FooterModal from '../../components/footerModal.vue';
import Meta from '../../components/Meta.vue';
import Hls from 'hls.js';
import { initializeApp } from "firebase/app";
import {
  getDatabase,
  ref,
  query,
  limitToLast,
  onChildAdded,
  onValue,
} from "firebase/database";

const firebaseConfig = {
  apiKey: "AIzaSyCjeff9NZCG3PzJs-5jiUFFtl_nYpA4mJE",
  authDomain: "hi8app.firebaseapp.com",
  databaseURL:
    "https://hi8app-default-rtdb.asia-southeast1.firebasedatabase.app",
  projectId: "hi8app",
  storageBucket: "hi8app.appspot.com",
  messagingSenderId: "1063832381376",
  appId: "1:1063832381376:web:daea698fb1eeeed54d0a25",
  measurementId: "G-DD8869YVY0",
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const database = getDatabase(app);
export default {
  components: { Meta, FooterModal },
  
  data () {
    return {
      paused : false,
      meta: {
        title:"HiBAR",
        image:"test image",
        description:"HiBAR",
      },
      data:{},
      hls:null,
      messages:[]
    }
  },

  mounted(){
    this.getVideo()
  },
  methods:{
    getVideo:function(){
      this.axios
        .get("https://im-api.hi8.tw/api/v1/channels/"+this.$route.params.id,{
          headers:{
            'Authorization': 'Bearer 1|f3V5j4KrPojChNLzgM5gUJrfXod7EqiTuKT6q5yY',
          }
        })
        .then((response)=>{
          this.hls = response.data.hls
          const video = document.getElementById('video');
          let videoSrc = this.hls;

          let hls = new Hls();
          hls.loadSource(videoSrc);
          hls.on(Hls.Events.ERROR,((event, data)=>{
              if (data.fatal) {
                  switch (data.type) {
                      case Hls.ErrorTypes.NETWORK_ERROR:
                          // try to recover network error
                          hls.destroy();
                          this.getVideo()
                          break;
                      case Hls.ErrorTypes.MEDIA_ERROR:
                          console.log('fatal media error encountered, try to recover');
                          hls.recoverMediaError();
                          break;
                      default:
                          // cannot recover
                          console.log('destroy');
                          hls.destroy();
                          break;
                      }
              }
          }))

          if (Hls.isSupported()) {
              hls.attachMedia(video);
          }else if (video.canPlayType('application/vnd.apple.mpegurl')){
              video.src = videoSrc;
          } 
          this.meta = {
              title:"HiBAR - 直播分享",
              image:this.data.user_image,
              description:"HiBAR - 直播分享",
          }
          this.getUser()
          this.callFirebase()
        })
        .catch(()=>{
            this.hls = null
        })
    },
    openModal:function(){
      this.$bvModal.show('my-modal')
    },
    play:function(){
      if(!this.paused){
        this.$refs.myVideo.play()
      }else{
        this.$refs.myVideo.pause()
      }
      this.paused = !this.paused
    },
    getUser:function(){
      this.axios
          .get("https://api.hi8.tw/hi8/v1/share/live/"+this.$route.params.id,
            {
                headers:{
                    'Apikey':"STEALING11111API22222NO33333DICK"
                }
            })
          .then((response)=>{
              this.data = response.data.data
              this.meta = {
                  title:"HiBAR - 帳號分享",
                  image:this.data.user_image,
                  description:this.data.user_description,
              }
          })
    },
    callFirebase:function(){
      onChildAdded(
        query(ref(database, this.$route.params.id+'/messages'), limitToLast(10)),
        (data) => {
          this.messages.push(data.val());
          this.scrollToBottom();
        }
      );

      onValue(ref(database, this.$route.params.id + '/user_count'), (data) => {
        this.$set(this.data,"user_count",  data.val())
      });

    },
    scrollToBottom() {
      setTimeout(() => {
        this.$refs.endOfContents.scrollIntoView();
      }, 50);
    },
  }
}
</script>
