<template>
  <div class="">
      <div class="logo"></div>
      <!-- <button type="button" class="close" @click="this.$bvModal.hide('my-modal')">
          <span aria-hidden="true">&times;</span>
      </button> -->
      <div class="content">
          <h5>想使用更多功能？</h5>
          <p>使用嗨吧帳號一起加入我們吧</p>
          <div class="btn" @click="openApp()"> 開啟嗨吧 </div>
      </div>
  </div>
</template>
<style lang="scss" scoped>
</style>
<script>

export default {
  props:{
    deepLink:null
  },
  data () {
    return {
    }
  },
  created(){
  },
  methods:{
    openApp:function(){
      const u = navigator.userAgent
      const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1
      if(isAndroid){
        window.location = this.deepLink; //schema link
        setTimeout(() => {
          location.href = "https://play.google.com/store/apps/details?id=com.hanchu.apps.hi8"
        }, 2000);
      }else if(isiOS){
        window.location = this.deepLink //schema link
        setTimeout(() => {
          location.href = "https://apps.apple.com/tw/app/%E5%97%A8%E5%90%A7/id1574032799"
        }, 2000);
      }
    }
  }
}
</script>