<template>
  <div class="activity">
    <Meta :meta="meta"/>
    <b-spinner label="Loading..." class="loadingIcon" v-if="!data"></b-spinner>
    <div class="mobileWeb mx-auto" @click="openModal()" v-else   style="height:800px;overflow: auto;"  @scroll="scrollEvent"> 
        <div class="banner text-center my-3">
            <p>{{ data.title }}</p>
            <img :src="data.banner_url">
        </div>
        <div class="activityInfo text-left">
            <p>活動時間 :{{ data.start_at }} ~ {{ data.end_at }}</p>

            <div>活動說明 :</div>
            <div v-html="descriptionMore?data.description:data.description.slice(0,80)+'....'"></div>
            <div class="cursor fs-12 text-right" @click.stop="descriptionMore = !descriptionMore">
                <span v-if="!descriptionMore">
                    查看詳情<b-icon icon="caret-down-fill"></b-icon>
                </span>
                <span v-else>
                    收起<b-icon icon="caret-up-fill"></b-icon>
                </span>
            </div>
            <div>活動獎勵 :</div>
            <div v-html="rewardMore?data.reward:data.reward.slice(0,80)+'....'"></div>
            <div class="cursor fs-12 text-right" @click.stop="rewardMore = !rewardMore">
                <span v-if="!rewardMore">
                    查看詳情<b-icon icon="caret-down-fill"></b-icon>
                </span>
                <span v-else>
                    收起<b-icon icon="caret-up-fill"></b-icon>
                </span>
            </div>

            <div>聲明 :</div>
            <div v-html="declarationMore?data.declaration:data.declaration.slice(0,80)+'....'"></div>
            <div class="cursor fs-12 text-right" @click.stop="declarationMore = !declarationMore">
                <span v-if="!declarationMore">
                    查看詳情<b-icon icon="caret-down-fill"></b-icon>
                </span>
                <span v-else>
                    收起<b-icon icon="caret-up-fill"></b-icon>
                </span>
            </div>
            
            <div class="hashTag">關聯話題 : 
                <span v-for="item,key in hashTag" :key="key">{{item}}</span>
            </div>
            <div>活動短影音:</div>
            <div class="d-flex fs-12">
                <div class="cursor">
                    台北市 <b-icon icon="caret-down-fill"></b-icon>
                    內湖區 <b-icon icon="caret-down-fill"></b-icon>
                </div>
                <div class="ml-auto cursor">
                    排序<b-icon icon="sort-up"></b-icon>
                </div>
            </div>
        </div>
        <div class="videoList pb-5">
            <div class="video" v-for="item,key in data.videos" :key="key" :style="{ backgroundImage: 'url(' + item.img_url + ')' }">
                <div class="likes">
                    <b-icon-play-fill style="color: #FFF;"></b-icon-play-fill>
                    <span class="text">{{ item.likeTOTAL }}</span>
                </div>
            </div>
            <!-- 每列不是三個影片時 佔版面位置 -->
            <div class="video" v-if="data.videos.length % 3 !== 0 "></div>
        </div>
        <b-modal id="my-modal" hide-footer hide-header dialog-class="openAppModal" content-class="openAppModalBody">
            <footer-modal :deep-link="'hi8://activity/?id='+$route.params.id"/>
        </b-modal>
    </div>
</div>
</template>
<style lang="scss" scoped>
