                    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.04) 17.19%, rgba(0, 0, 0, 0.2) 54.17%, rgba(0, 0, 0, 0.4) 100%);
