</style>
<script>
import FooterModal from '../../components/footerModal.vue';
import Meta from '../../components/Meta.vue';

export default {
    components: { Meta, FooterModal },
    
    data () {
        return {
            descriptionMore:false,
            rewardMore:false,
            declarationMore:false,
            getVideoPage:1,
            meta: {
                title:"HiBAR",
                image:"test image",
                description:"HiBAR",
            },
            data:{
                description:"",
                reward:"",
                declaration:"",
                relation_tag:"",
                videos:[]
            }
        }
    },
    mounted(){
        this.axios
            .post("https://api.hi8.tw/hi8/v1/event/activity",{
                "id": this.$route.params.id,
            },
            {
                headers:{
                    'apiKey':"STEALING11111API22222NO33333DICK"
                }
            })
            .then((response)=>{
                response.data.data.videos = []
                this.data = response.data.data

                this.meta = {
                    title:"HiBAR - 活動分享",
                    image:this.data.banner_url,
                    description:this.data.reward,
                }
            })
        this.getVideo()
        
    },
    computed:{
        hashTag:function(){
            return this.data.relation_tag.split(" ")
        },
    },
    methods:{
        openModal:function(){
            this.$bvModal.show('my-modal')
        },
        getVideo:function(){
            this.axios
                .post("https://api.hi8.tw/hi8/v1/event/topicVideoListPublic",{
                    "id": this.$route.params.id,
                    "sort": 2,
                    "page":this.getVideoPage,
                },
                {
                    headers:{
                        'apiKey':"STEALING11111API22222NO33333DICK"
                    }
                })
                .then((response)=>{
                    if(response.data.data.length !== 0){
                        this.getVideoPage += 1
                        this.data.videos = this.data.videos.length === 0? response.data.data:this.data.videos.concat(response.data.data)
                    }
                })

        },
        scrollEvent:function(e){
            if(e.srcElement.scrollHeight - e.srcElement.scrollTop - e.srcElement.offsetHeight === 0){
                this.getVideo()
            }
        }
    }
}
</script>